import { RestDataSource } from './rest.datasource';
import { Injectable } from '@angular/core';
import { User } from './user.model';
import { Observable } from 'rxjs';

@Injectable()
export class UserRepository {
  private user: User[] = [];
  private username: string [];

  // tslint:disable-next-line: variable-name
  constructor(private user_dataSource: RestDataSource) {}

  getUser(username: string = null): User[] {
    return this.user.filter(b => username == null || username === b.username);
  }

  addUser(thisUser: User): Observable<any> {
    return this.user_dataSource.addUser(thisUser);
  }
}
